<template>
  <div class="catalog">
    <Navbar />
    <h1 class="catalog-title text-center m-4">
      Каталог
    </h1>
    <form class="search">
      <input
        v-model="search"
        class="input-search"
        type="text"
        placeholder="Искать здесь..."
      >
      <button
        class="button-search"
        type="submit"
      >
        <img
          class="img-search"
          src="@/img/search.svg"
          alt
        >
      </button>
    </form>
    <div class="container p-0 mt-3">
      <div class="d-flex flex-row justify-content-center">
        <div class="col-xl col-lg-2 col-md-3 d-none d-md-block mr-3 mt-1">
          <div class="row">
            <div class="col-xl-12 col-lg-12 col-md-12 d-none d-md-block mr-3 mb-3 p-0">
              <div class="text-center banner1 p-0 m-0">
                <router-link to="/sales">
                  <img
                    src="@/img/banner/153x515.png"
                    class="img-fluid"
                    style="height: 517px; width: 200px;"
                    alt
                  >
                </router-link>
              </div>
            </div>
            <div class="col-xl-12 col-lg-12 col-md-12 d-none d-md-block border mr-3 p-0">
              <div class="text-center banner1 p-0 m-0">
                <router-link to="/sales">
                  <img
                    src="@/img/banner/153х800.png"
                    class="img-fluid"
                    style="height: 672px; width: 200px;"
                    alt
                  >
                </router-link>
              </div>
            </div>
          </div>
        </div>
        <div class="col-xl-8 col-lg-7 col-md-9 col-sm-12 col-12">
          <div class="row">
            <div
              v-for="(inf, i) in todosByTitle"
              :key="i"
              class="col-xl-4 col-lg-6 col-md-6 col-sm-6 col-6 d-flex justify-content-center mt-3"
            >
              <div class="card">
                <div class="m-1 p-1 d-flex align-items-center card-img-size">
                  <img
                    style="cursor: pointer"
                    draggable="false"
                    :src="`http://bohmann.shop${inf.images[0]}`"
                    class="img-fluid zz"
                    alt="..."
                    @click="goToproduct(inf.id)"
                  >
                </div>
                <div class="card-body p-1 d-flex align-items-center justify-content-center">
                  <h5
                    class="card-title text-center"
                    style="cursor: pointer"
                    @click="goToproduct(inf.id)"
                  >
                    {{ inf.name }}
                  </h5>
                </div>
                <div class="m-1">
                  <p class="card-text text-center">
                    {{ inf.price }} ₴
                    <b-img
                      v-if="log == 'true'"
                      right
                      class="favor"
                      title="В список желаний"
                      :src="require('../img/favorite.svg')"
                      @click="addToFavorite(inf)"
                    />
                  </p>
                  <button
                    type="button"
                    class="btn btn-primary card-btn btn-block"
                    @click="addToCart(inf)"
                  >
                    {{ titleB }}
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div class="pagination">
            <a @click="firstPage">«</a>
            <a @click="Page1">1</a>
            <a @click="Page2">2</a>
            <a @click="Page3">3</a>
            <a @click="Page4">4</a>
            <a @click="lastPage">»</a>
          </div>
          <!-- pg -->
        </div>
        <div class="col-xl- col-lg-2 d-none d-lg-block ml-3 mt-1">
          <AccountWindow />
        </div>
      </div>
    </div>
    <div class="container border-top p-0 mt-3">
      <div class="recom text-center p-4">
        Рекомендуемые
      </div>
      <div class="d-flex flex-row justify-content-center">
        <div class="col-xl col-lg-2 col-md-3 d-none d-md-block mr-3 border p-0">
          <div class="text-center banner1 p-0 m-0">
            <router-link to="/sales">
              <img
                src="@/img/banner/153x515.png"
                class="img-fluid"
                style="height: 587px; width: 153px;"
                alt
              >
            </router-link>
          </div>
        </div>
        <div class="col-xl-8 col-lg-7 col-md-9 col-sm-12 col-12 p-0">
          <div class="container">
            <div class="row">
              <div class="col-12 col-8-12 text-center">
                <sl :info="info" />
              </div>
              <div class="col-12 col-xl-12 text-center">
                <div class="text-center p-0 mt-5">
                  <a href="#">
                    <img
                      src="@/img/banner/740х139.png"
                      class="img-fluid"
                      style="height: 139px; width: 740px;"
                      alt
                    >
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-xl- col-lg-2 d-none d-lg-block ml-3 border p-0">
          <div class="text-center banner1 p-0 m-0">
            <router-link to="/sales">
              <img
                src="@/img/banner/185x670.png"
                class="img-fluid"
                style="height: 589px; width: 185px;"
                alt
              >
            </router-link>
          </div>
        </div>
      </div>
      <div class="d-flex flex-row justify-content-center">
        <div
          class="col-xl-12 col-lg-12 col-12 border text-center mt-5 d-flex justify-content-center p-0"
        >
          <div class="text-center banner1 p-0 m-0">
            <a href="#">
              <img
                src="@/img/banner/1110x190.png"
                class="img-fluid"
                style="width: 1137px;"
                alt
              >
            </a>
          </div>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Navbar from "./Navbar.vue";
import Footer from "./Footer.vue";
import AccountWindow from "./AccountWindow.vue";
import sl from "./slider.vue";
import axios from "axios";
export default {
    /*eslint no-undef: 0*/
    name: "Catalg",
    components: {
        Navbar,
        Footer,
        AccountWindow,
        sl
    },
    props: {
        info: {
            type: Array
        }
    },
    data() {
        return {
            x: "https://bomann.shop",
            text: "",
            showModalOne: false,
            showModalTwo: false,
            search: "",
            titleB: "Купить",
            disableButton: false,
            initialCount: 1,
            log: this.$store.state.login,
            cc: []
        };
    },
    computed: {
        todosByTitle() {
            return this.cc.filter(item => item.name.indexOf(this.search) !== -1);
        }
    },
    mounted() {
        axios
            .get(`http://bohmann.shop/api/products/?limit=15`)
            .then(response => (this.cc = response.data.results));
    },
    methods: {
        goToproduct(Id) {
            this.$router.push({ name: "product", params: { Pid: Id } });
        },
        addToCart(item) {
            this.$store.commit("addToCart", item);
            let found = this.$store.state.cart.find(
                product => product.id === item.id
            );
            found.inCart = true;
        },
        addToFavorite(item) {
            this.$store.commit("addToFavorite", item);
            let foun = this.$store.state.favorite.find(
                product => product.id === item.id
            );
            foun.inFavorite = true;
        },
        firstPage() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15`)
                .then(response => (this.cc = response.data.results));
        },
        Page1() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15`)
                .then(response => (this.cc = response.data.results));
        },
        Page2() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15&offset=15`)
                .then(response => (this.cc = response.data.results));
        },
        Page3() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15&offset=30`)
                .then(response => (this.cc = response.data.results));
        },
        Page4() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15&offset=45`)
                .then(response => (this.cc = response.data.results));
        },
        lastPage() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15&offset=45`)
                .then(response => (this.cc = response.data.results));
        }

    // clickCallback: function() {
    //   console.log(this.counter);
    //   axios
    //     .get(`http://192.168.1.123:8000/api/products/?limit=10&offset=10`)
    //     .then(response => (this.counter = response.data.results));
    //   this.counter = this.info;
    //   console.log(this.counter);
    //   // console.log((this.counter = this.info));
    // }
    }
};
</script>
